import axios from 'axios';
import env from '../config/env';
import PostCard from '../components/PostCard';
export default {
    name: 'IndexPage',
    components:{
    	PostCard
    },
    data() {
    	return {
    		posts: []
    	}
    },
    created: async function(){
		let postsResponse = await axios.get(`${env.endpoint}/posts`);
		this.posts = postsResponse.data;
	}
}
